$primaryColor: rgb(185, 185, 185);
$cardColor: radial-gradient(#1e0101, #840b3a);
body{
    text-align: center;
}
h1{
    padding-top: 50px;
    font-size: 50px;
   
}

.main{
    background-color: $primaryColor;
    
    height: 72vh;
    margin-top: 50px;
    
   
    
    .btn{
        margin: 30px;
        margin-top: 80px;
       
        width: 100px;
        border-radius: 50px;
    }
    
    
  
}
.cards{
    
    display: flex;
    justify-content: space-evenly;
    .card{
        height: 150px;
        margin-top: 50px;
        background: $cardColor;
        box-shadow: 2px 0 5px 0;
        .card-body{
            color: white;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .answer{
            height: 30px;
            width: 30px;
            
            position: absolute;
            top: 0;
            right: 0;
            border-radius: 5px;
            
           }
        
    }
  
 
}



